import React, { useEffect, useState } from 'react';
import { green_elephant_parts } from '../green_elephant_source';

const getRandomPart = () => green_elephant_parts[Math.floor(Math.random() * green_elephant_parts.length)];

type Puzzle = {
    puzzle: string;
    answer: string;
    solver: (str: string) => boolean;
}

const createPuzzle = (part: string = getRandomPart()): Puzzle => {
    const parts = part.split(" ");
    const random_part_index = Math.floor(Math.random() * parts.length);
    const puzzle = parts[random_part_index].replace(/[,.?!«»…]/g, "");
    if (puzzle.length < 2) {
        return createPuzzle();
    }

    parts[random_part_index] = parts[random_part_index].replace(puzzle, "*".repeat(puzzle.length));
    return {
        puzzle: parts.join(" "),
        answer: part,
        solver: (str: string) => str.toUpperCase() === puzzle.toUpperCase(),
    }
};

type Props = {};

export const RandomPhrase: React.FC<Props> = () => {
    const [{ puzzle, answer, solver }, setPuzzle] = useState(createPuzzle);
    const [value, setValue] = useState("");
    const [answers, setAnswers] = useState(0);
    const [errors, setErrors] = useState(0);
    const [classic, setClassic] = useState("");

    useEffect(() => {
        if (!classic) {
            return;
        }
        const clear_timeout = setTimeout(() => setClassic(""), 5000);

        return () => clearTimeout(clear_timeout);
    }, [classic]);

    const solve = () => {
        if (solver(value)) {
            setAnswers(answers + 1);
        } else {
            setErrors(errors + 1);
            setClassic(answer);
        }
        setPuzzle(createPuzzle());
        setValue("");
    }

    return (
        <div className="RandomPhrase">
            <div className="score">
                <div className="answers">Правильных ответов: {answers}</div>
                <div className="errors">Ошибок: {errors}</div>
            </div>
            <div className="puzzle">{puzzle}</div>
            <div className="inputWrapper">
                <input value={value} onChange={e => setValue(e.target.value)} onKeyDown={e => e.keyCode === 13 && solve()} />
            </div>
            {classic && <div className="classic">
                {classic}
            </div>}
        </div>
    );
};

